import { Component } from 'react'
import './App.css';
// URL, a title, and a "tag"
class BookMarking extends Component{

  constructor(props) {
    super(props);

    this.state = {
      title: '',
      tag: '',
      url: '',
    }

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleInput = this.handleInput.bind(this);
  }

  handleSubmit(event) {
    event.preventDefault();
    this.props.addUrl(this.state);
    this.setState({
      title: '',
      tag: '',
      url: '',
    });
  }

  handleInput(event) {
    this.setState({ [event.target.name]: event.target.value });
  }

  render() {
    return(
      <form onSubmit={this.handleSubmit}>
        <input type="text" name="title" value={this.state.title} onChange={this.handleInput}/>
        <input type="tag" name="url" value={this.state.tag} onChange={this.hanldeInput}/>
        <input type="url" name="url" value={this.state.url} onChange={this.handleInput}/>
        <button type="submit">Save</button>
      </form>
    )
  }
};

class UrlList extends Component{

  render() {
    const urls = this.props.urls.map((url, index) => (
      <li key={index}>
        <img src={url.url} alt=""/>
        <p>{url.caption}</p>
      </li>
    ));

    return(
      <ul>
        {urls}
      </ul>
    )
  }
};

